:root {
  --white: #ffffff;
  --green: #20e4ab;
  --header-text: 2em;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/**************************** Header style ***************/

.header-container {
  background-color: var(--green);
  width: auto;
  height: 13vh;
  box-shadow: 0 1px 10px grey;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 80px;
}

.header-name {
  color: var(--white);
  font-size: var(--header-text);
  font-weight: 500;
  text-align: left;
  padding: 0px 20px;
}

.header-ttt-info {
  padding: 0px 20px;
  display: flex;
  align-items: center;
}

.ttt-name {
  font-size: var(--header-text);
  color: var(--white);
  font-weight: 700;
}

.logo-ttt {
  height: 8vh;
  min-height: 70px;
}

/************************ button style ********************/

.btn-container {
  background-color: var(--green);
  text-align: center;
  border-radius: 10px;
  padding: 30px 50px;
  box-shadow: 0 5px 10px grey;
  border: none;
}

.btn-container:hover {
  background-color: var(--green);
  text-align: center;
  border-radius: 10px;
  padding: 30px 50px;
  box-shadow: 0 3px 10px grey;
  border: none;
}

.btn-text {
  color: var(--white);
  font-size: 2em;
  font-weight: 700;
}

/************************ text style ********************/
#form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 80vh;
  width: 100vw;
  min-height: 200px;
}

.text-area {
  padding: 100px;
  width: 90%;
  height: 70%;
  line-height: 1.5;
  border-radius: 5px;
  border: 1px solid var(--green);
}

/************************ Login ********************/

#global-container-login {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
}

.label-login {
  font-weight: 600;
  color: var(--green);
}

.input-email {
  margin: 0px 2px;
  padding: 4px;
  border: 1px solid var(--green);
  border-radius: 2px;
}

.btn-login {
  background-color: var(--green);
  color: white;
  font-weight: 600;
  text-align: center;
  border-radius: 3px;
  padding: 5px;
  margin: 1px;
  /* box-shadow:0 5px 10px grey; */
  border: none;
}

.btn-login:hover {
  background-color: var(--white);
  color: var(--green);
  font-weight: 600;
  text-align: center;
  border-radius: 3px;
  padding: 3px;
  margin: 1px;
  /* box-shadow:0 5px 10px grey; */
  border: 2px solid var(--green);
}
